<template>
  <section id="hero">
    <v-container>
      <v-row>
        <v-col>
          <v-sheet min-height="70" rounded="0">
            <v-carousel
              cycle
              height="400"
              hide-delimiter-background
              show-arrows-on-hover
            >
              <template v-slot:prev="{ on, attrs }">
                <v-btn color="grey lighten-1" v-bind="attrs" v-on="on">
                  <v-icon>mdi-arrow-left-bold-hexagon-outline</v-icon>
                </v-btn>
              </template>
              <template v-slot:next="{ on, attrs }">
                <v-btn color="grey lighten-1" v-bind="attrs" v-on="on">
                  <v-icon>mdi-arrow-right-bold-hexagon-outline</v-icon>
                </v-btn>
              </template>
              <v-carousel-item v-for="(slide, i) in slides" :key="i">
                <v-sheet :color="colors[i]" height="100%">
                  <v-row class="fill-height" align="center" justify="center">
                    <div class="display-3">{{ slide }}...</div>
                  </v-row>
                </v-sheet>
              </v-carousel-item>
            </v-carousel>
            <v-container class="content" v-if="contentList.length">
              <div class="columns is-centered is-multiline">
                <div v-for="content in contentList" :key="content" class="card column is-one-quarter">
                  <ContentCard :content="content"></ContentCard>
                </div>
              </div>
            </v-container>
            <v-container v-if="!contentList.length" class="not-found">
              <div class="not-found__content">
                <h2 class="not-found__title">Ничего не найдено</h2>
              </div>
            </v-container>
          </v-sheet>
        </v-col>
      </v-row>
    </v-container>
  </section>
</template>

<script>

import ContentCard from '@/components/ContentCard'
import api from '../../../api/api'

export default {
  name: "SectionHero",
  components: {ContentCard},

  data: () => ({
    drawer: null,
    colors: [
      "indigo",
      "warning",
      "pink darken-2",
      "red lighten-1",
      "deep-purple accent-4",
    ],
    slides: ["news", "news", "news", "news", "news"],
    date: new Date().toISOString().substr(0, 15),
    pickerDate: null,
    notes: [],
    allNotes: [
      "President met with prime minister",
      "New power plant opened",
      "Rocket launch announced",
      "Global warming discussion cancelled",
      "Company changed its location",
    ],

    watch: {
      pickerDate() {
        this.notes = [
          this.allNotes[Math.floor(Math.random() * 5)],
          this.allNotes[Math.floor(Math.random() * 5)],
          this.allNotes[Math.floor(Math.random() * 5)],
        ].filter((value, index, self) => self.indexOf(value) === index);
      },
    },
    contentList: []
  }),

  computed: {
    minHeight() {
      const height = this.$vuetify.breakpoint.mdAndUp ? "100vh" : "50vh";

      return `calc(${height} - ${this.$vuetify.application.top}px)`;
    },
  },
  mounted () {
    api.Content.list().then(result => result.forEach(content => {
      this.contentList.push(content);
    }));
  },
};
</script>

<style>
  .column {
    margin: 5px;
    padding: 0;
  }
</style>
