<template>
 <v-card>
  <div class="card">
    <div v-if="content.id % 2 != 0">
      <div class="color-block" :style="{background: this.colors[Math.floor(Math.random() * this.colors.length)]}">
      </div>
    </div>
    <div v-if="content.id % 2 == 0">
      <div class="card-image">
        <figure class="image is-16by9">
          <img class="content-image" :src="require(`../static/ContentImgs/${content.img}`)" alt="Placeholder image">
        </figure>
      </div>
    </div>
    <div class="card-content">
      <div class="media">
        <div class="media-content">
          <p class="title is-4">{{ content.title }}</p>
        </div>
      </div>
    </div>
  </div>
  <router-link class="details" :to="{ name: 'content', params: { contentId: content.id }}"></router-link>
 </v-card>
</template>

<script>
export default {
  name: 'contentCard',
  props: ['content'],

  data () {
    return {
      colors: [
        "indigo",
        "red",
        "green",
        "blue",
        "orange",
        "purple",
        "yellow"
      ],
    }
  }
}
</script>

<style>
  .color-block{
    height: 100px;
    border-radius: 3px;
  }
  .card {
    height: 200px;
    padding: 3px;
  }
  .card-image {
    width: 100%;
  }
  .image {
    border-radius: 3px;
    margin: 0 !important;
    padding: 50px !important;
  }
  .content-image {
    height: 100px !important;
  }
  .details {
    cursor: pointer;
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    padding: 5px;
    border-radius: 3px;
  }
  .details:hover {
    border: 1px solid #6b7aff;
  }
 
  .button,
  .select {
    z-index: 2;
  }
  .select {
    position: absolute;
    right: 15px;
    bottom: 35px;
  }
  .buttons {
    margin: 0;
  }
</style>
